<template>
    <div class="article-admin">
        <b-form>
            <input id="article-id" type="hidden" v-model="article.id" />
            <b-form-group label="Nome:" label-for="article-name">
                <b-form-input id="article-name" 
                        type="text"
                        v-model="article.name"
                        required
                        :readonly="mode === 'remove'"
                        placeholder="Informe o Nome do Artigo...">
                </b-form-input>
            </b-form-group>
            <b-form-group label="Descrição:" label-for="article-description">
                <b-form-input id="article-description" 
                        type="text"
                        v-model="article.description"
                        required
                        :readonly="mode === 'remove'"
                        placeholder="Informe a Descrição do Artigo...">
                </b-form-input>
            </b-form-group>
            <b-form-group v-if="mode === 'save'"
                    label="Imagem (URL):" label-for="article-image-url">
                <b-form-input id="article-image-url" 
                        type="text"
                        v-model="article.imageUrl"
                        required
                        :readonly="mode === 'remove'"
                        placeholder="Informe a URL da imagem...">
                </b-form-input>
            </b-form-group>
            <b-form-group v-if="mode === 'save'" 
                        label="Categoria:" label-for="article-categoryId">
                <b-form-select id="article-categoryId"
                            v-model="article.categoryId"
                            :options="categories">
                </b-form-select>
            </b-form-group>
            <b-form-group v-if="mode === 'save'" 
                        label="Autor:" label-for="article-userId">
                <b-form-select id="article-userId"
                            v-model="article.userId"
                            :options="users">
                </b-form-select>
            </b-form-group>
            <b-form-group v-if="mode === 'save'"  label="Conteúdo" label-for="category-content">
                <VueEditor v-model="article.content"
                        placeholder="Informe o Conteúdo do Artigo...">
                </VueEditor>
            </b-form-group>
                        <b-button variant="primary"
                        v-if="mode === 'save'"
                        @click="save">
                Salvar
            </b-button>
            <b-button variant="danger"
                        v-if="mode === 'remove'"
                        @click="remove">
                Excluir
            </b-button>
            <b-button class="ml-2" @click="reset">Cancelar</b-button>
        </b-form>
        <hr>
        <b-table responsive hover striped :items="articles" :fields="fields">
            <template v-slot:cell(actions)="data">
                <b-button variant="warning" @click="loadArticle(data.item)" class="mr-2">
                    <i class="fa fa-pencil"></i>
                </b-button>
                <b-button variant="danger" @click="loadArticle(data.item, 'remove')">
                    <i class="fa fa-trash"></i>
                </b-button>
            </template>
        </b-table>
        <b-pagination size="md" 
                      v-model="page" 
                      :total-rows="count"
                      :per-page="limit">
        </b-pagination>
    </div>
</template>
<script>
import { VueEditor } from 'vue2-editor'
import { baseApiUrl, showError } from '@/global'
import axios from 'axios'
export default {
    name: 'ArticleAdmin',
    components: { VueEditor },
    data: function() {
        return {
            mode: 'save',
            article: {},
            articles: [],
            categories: [],
            users: [],
            page: 1,
            limit: 0,
            count: 0,
            fields: [
                { key: 'id', label: 'Código', sortable: true },
                { key: 'name', label: 'Nome', sortable: true },
                { key: 'description', label: 'Descrição', sortable: true },
                { key: 'actions', label: 'Ações' }
            ]
        }
    },
    methods: {
        loadArticles() {
            const url = `${baseApiUrl}/articles?page=${this.page}`
            axios.get(url).then(res => {
                this.articles = res.data.data
                this.count = res.data.count
                this.limit = res.data.limit
            })
        },
        reset() {
            this.mode = 'save'
            this.article = {}
            this.loadArticles()
        },
        save() {
            const method = this.article.id ? 'put' : 'post'
            const id = this.article.id ? `/${this.article.id}` : ''
            const url = `${baseApiUrl}/articles${id}`
            axios[method](url, this.article)
                .then(() => {
                    this.$toasted.global.defaultSuccess()
                    this.reset()
                })
                .catch(showError)
        },
        remove() {
            const id = this.article.id
            const url = `${baseApiUrl}/articles/${id}`
            axios.delete(url)
                .then(() => {
                    this.$toasted.global.defaultSuccess()
                    this.reset()
                })
                .catch(showError)
        },
        loadArticle(article, mode = 'save') {
            this.mode = mode
            const id = `/${article.id}`
            const url = `${baseApiUrl}/articles${id}`
            axios.get(url)
            .then(res => this.article = res.data)
        },
        loadCategories() {
            const url = `${baseApiUrl}/categories`
            axios.get(url).then(res => {
                this.categories = res.data.map(category => {
                    return { value: category.id, text: category.path }
                })
            })
        },
        loadUsers() {
            const url = `${baseApiUrl}/users`
            axios.get(url).then(res => {
                this.users = res.data.map(user => {
                    return { value: user.id, text: `${user.name} - ${user.email}` }
                })
            })
        }
    },
    watch: {
        page() {
            //console.log(this.page);
            this.loadArticles()
        }
    },
    mounted() {
        this.loadUsers(),
        this.loadCategories(),
        this.loadArticles()
    },
}
</script>
